import React, { useState, useEffect } from "react";
import ReactDOM from "react-dom";
import "../node_modules/bootstrap/dist/css/bootstrap.min.css";

function App() {
  const [Data, setData] = useState([]);

  useEffect(() => {
    allData();
  }, []);

  function allData() {
    fetch(
      "https://libraries.io/api/platforms?api_key=cb340061c0950b06fd48e3bba4bc2467"
    )
      .then(res => res.json())
      .then(data => setData(data));
  }

  return (
    <div className="container text-center mt-4">
      <h3 className="head mb-3">Library Information</h3>
      <div className=" row m-2">
        {Data.map((data, index) => (
          <div key={index} className=" col-md-4 shadow-sm my-2 p-4">
            <h4 className="underline pb-1">{data.name}</h4>
            <p className="lead">Projects :{data.project_count}</p>
            <a href={data.homepage} target="_blank" rel="noopener noreferrer">
              {data.homepage}
            </a>
            {data.default_language && (
              <div className="my-2">
                Default Language : {data.default_language}
              </div>
            )}
          </div>
        ))}
      </div>
    </div>
  );
}

const rootElement = document.getElementById("root");
ReactDOM.render(<App />, rootElement);
